import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import VueResource from 'vue-resource'

// components
import index from './components/index.vue'
import error404 from './components/error404.vue'
import content2 from './components/content2.vue'
import content3 from './components/content3.vue'
import content4 from './components/content4.vue'
import content5 from './components/content5.vue'
import content6 from './components/content6.vue'
import content7 from './components/content7.vue'


// Vue.config.debug = true;

Vue.use(VueRouter);
Vue.use(VueResource);

// 
const RouterConfig = {
  mode: 'history',
  base: __dirname,
  routes: [
    {
      path: '/',
      component: index
      // redirect: '/index'
    },
    {
      path: '*',
      component: error404
    },
    {
      path: '/content2',
      component: content2
    },
    {
      path: '/content3',
      component: content3
    },
    {
      path: '/content4',
      component: content4
    },
    {
      path: '/content5',
      component: content5
    },
    {
      path: '/content6',
      component: content6
    },
    {
      path: '/content7',
      component: content7
    }
  ]
};

const router = new VueRouter(RouterConfig);

router.afterEach(route => { //
  sessionStorage.index = 11; //
  sessionStorage.navIndex = 'menu1';
})

/* eslint-disable no-new */
var vm = new Vue({
  el: '#app',
  template: '<App/>',
  components: {
    App
  },
  router: router
})



